import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import axios from "axios";
import { toast } from "react-toastify";
const DashBoard = () => {
  const [foundUser, setFoundUser] = useState([]);
  const [role, setRole] = useState("");
  const [getId, setId] = useState("");
  const [foundCompany, setFoundCompany] = useState([]);
  const [foundJob, setFoundJob] = useState([]);

  useEffect(() => {
    const fetchCompany = async () => {
      try {
        const url = `/api/getData`;
        const response = await axios.get(url);
        setFoundCompany(response.data);
      } catch (error) {
        console.log(error);
      }
    };
    fetchCompany();
  }, []);
  useEffect(() => {
    const fetchJob = async () => {
      try {
        const url = `/api/getJobs`;
        const response = await axios.get(url);
        setFoundJob(response.data);
      } catch (error) {
        console.log(error);
      }
    };
    fetchJob();
  }, []);

  const handleRole = async (e) => {
    try {
      const url = `/api/setRole/${getId}`;
      const response = axios.put(url, { role });
      console.log(response.data);
    } catch (error) {
      console.log(error);
    }
  };

  useEffect(() => {
    const fethUser = async () => {
      try {
        const url = `/api/getAllusers`;
        const response = await axios.get(url);
        setFoundUser(response.data);
      } catch (error) {
        console.log(error);
      }
    };
    fethUser();
  }, []);

  const handleDelete = async (e) => {
    e.preventDefault();
    setId(e.target.id);

    try {
      const url = `/api/deleteUser/${getId}`;
      const response = await axios.delete(url);
      toast.success("User Deleted Successfully");
      console.log(response.data);
    } catch (error) {
      console.log(error);
    }
  };

  return (
    <div
      class="container-fluid"
      id="main"
      style={{
        marginLeft: "1rem",
      }}
    >
      <div class="row row-offcanvas row-offcanvas-left">
        <div
          class="col-md-3 col-lg-2 sidebar-offcanvas"
          id="sidebar"
          role="navigation"
          style={{
            border: "1px solid #ccc",
          }}
        >
          <ul class="nav flex-column pl-1 p-2">
            <li class="nav-item text-bg-secondary p-3">Overview</li>
            <li class="nav-item p-3">
              <Link to="/admin/data" class="list-group-item  main-color-bg ">
                Dashboard
              </Link>
            </li>
            <li class="nav-item p-3">
              <Link to="/admin/data/add-jobs" class="list-group-item ">
                Add Jobs
              </Link>
            </li>
            <li class="nav-item p-3">
              <Link to="/admin/data/add-company" class="list-group-item">
                Add Company
              </Link>
            </li>
            <li class="nav-item p-3">
              <Link to="/admin/data/add-internships" class="list-group-item">
                Add Internships
              </Link>
            </li>
            <li class="nav-item p-3">
              <Link to="/forgot-password" class="list-group-item ">
                Change Password
              </Link>
            </li>
            <li class="nav-item p-3">
              <Link to="/forgot-password" class="list-group-item ">
                Users
              </Link>
            </li>
          </ul>
        </div>

        <div class="col-md-9 col-lg-9 main">
          <h1 class="text-bold m-3">Admin Dashboard</h1>

          <div class="row mb-3 text-center mt-2">
            <div class="col-xl-3 col-lg-6">
              <div class="card card-inverse card-success">
                <div class="card-block bg-success">
                  <div class="rotate mt-2">
                    <i class="fa fa-user fa-3x"></i>
                  </div>
                  <h6 class="text-uppercase">Users</h6>
                  <h1 class="display-1">{foundUser.length}</h1>
                </div>
              </div>
            </div>
            <div class="col-xl-3 col-lg-6">
              <div class="card card-inverse card-danger">
                <div class="card-block bg-danger">
                  <Link
                    to="/admin/data/add-company"
                    class="list-group-item text-dark"
                  >
                    <div class="rotate mt-2">
                      <i class="fa fa-list fa-3x"></i>
                    </div>

                    <h6 class="text-uppercase">Companies</h6>
                    <h1 class="display-1">{foundCompany.length}</h1>
                  </Link>
                </div>
              </div>
            </div>
            <div class="col-xl-3 col-lg-6">
              <div class="card card-inverse card-info">
                <div class="card-block bg-info">
                  <Link
                    to="/admin/data/add-jobs"
                    class="list-group-item text-dark"
                  >
                    <div class="rotate mt-2">
                      <i class="fa fa-building-o fa-3x"></i>
                    </div>
                    <h6 class="text-uppercase">Jobs</h6>
                    <h1 class="display-1">{foundJob.length}</h1>
                  </Link>
                </div>
              </div>
            </div>
            <div class="col-xl-3 col-lg-6">
              <div class="card card-inverse card-warning">
                <div class="card-block bg-warning">
                  <div class="rotate mt-2">
                    <i class="fa fa-share fa-3x"></i>
                  </div>
                  <h6 class="text-uppercase">Course</h6>
                  <h1 class="display-1">36</h1>
                </div>
              </div>
            </div>
          </div>

          <hr />

          <hr />

          <form onSubmit={handleRole}>
            <div class="table-responsive-lg">
              <table className="table table-striped">
                <thead>
                  <tr>
                    <th scope="col">Index</th>
                    <th>Name</th>
                    <th>Email</th>
                    <th>Role</th>
                    <th>Delete</th>
                    <th>Edit</th>
                    <th>Give Role</th>
                  </tr>
                </thead>
                <tbody>
                  {foundUser.map((user, index) => {
                    return (
                      <tr>
                        <td scope="row">{index + 1}</td>
                        <td>{user.name}</td>
                        <td>{user.email}</td>
                        <td>{user.role}</td>
                        <td>
                          <button
                            class="btn btn-danger"
                            id={user._id}
                            onClick={handleDelete}
                          >
                            Delete
                          </button>
                        </td>
                        <td>
                          <button class="btn btn-success">Edit</button>
                        </td>
                        <td>
                          <select
                            class="form-control"
                            onChange={(e) => {
                              // alert(e.target.value);
                              setId(user._id);
                              setRole(e.target.value);
                            }}
                          >
                            <option disabled selected>
                              Select Role
                            </option>
                            <option value="admin">Admin</option>
                            <option value="user">User</option>
                            <option value="recruiter">Recruiter</option>
                          </select>
                        </td>
                        <td>
                          <button class="btn btn-primary">Submit</button>
                        </td>
                      </tr>
                    );
                  })}
                </tbody>
              </table>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
};

export default DashBoard;
